@use 'base/variables'  as v;
@use 'base/mixins'  as m;


.contenedor-resumen{
    p{
        margin-bottom: 1rem;
    }
    span{
        color: v.$rosafosfo;
        font-weight: v.$bold;
        font-size: 2.4rem;
    }
}


.contenedor-producto{
    padding: 3rem 0;
    border-bottom: 1px solid v.$gris;

    &:last-of-type{
        margin-bottom: v.$separacion;
    }
}
