@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-productos{

    display: grid;
    gap: 2rem;
    @include m.tablet{
        @include m.grid(2, 2rem);
    }
    .producto{

        background-color: v.$blanco;
        padding: 3rem;
        border-radius: 1rem ;
        transition-property: transform;
        transition-duration: 300ms;
        text-align: center;

        
        &.seleccionado{
            background-color: v.$rosafosfo;
            p{
                color: v.$blanco;
            }
        }

        &:hover{
            transform: scale(1.1);
        }

        p{
            color: v.$negro;
            margin: 0 0 calc(v.$separacion/4) 0;
        }

        .precio-producto{
            color: v.$moradomamalon;
            font-weight: v.$bold;
            font-size: 2rem;
            margin: 0;
        }
    }
}
